import * as React from "react";
import { makeStyles, createStyles, Theme } from "@material-ui/core/styles";
import {
  Table,
  TableContainer,
  TableCell,
  TableRow,
  TableHead,
  TableBody,
  Paper,
  TextField,
  NativeSelect,
  FormControl,
  Grid,
} from "@material-ui/core";
import { NumericFormat as NumberFormat } from "react-number-format";

import { TableCalculate } from "../../common";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    table: {
      minWidth: 650,
    },
    formControl: {
      margin: theme.spacing(2),
      minWidth: 120,
      display: "grid",
    },
    sixPercent: {
      backgroundColor: "#eee",
    },
    tenPercent: {
      // backgroundColor: '#ddd',
    },
    rowTotal: {
      color: "red !important" as "red",
      fontWeight: "bold !important" as "bold",
      backgroundColor: "rgba(206, 17, 38, 0.05)",
    },
    selectBox: {
      marginTop: "0 !important",
    },
  })
);
interface Props {
  onRefreshCalculate: (data: any) => void;
  tableCalculate: TableCalculate;
}

export const TableCalculateComponent: React.FunctionComponent<Props> = (
  props
) => {
  let onChangeField = (name: string, value: unknown | null) => {
    value = Number(value);
    if (!value) {
      value = null;
    }
    props.onRefreshCalculate({ [name]: value });
  };

  let onChangeSelect = (
    e: React.ChangeEvent<{ name: string; value: unknown }>
  ) => {
    let value = Number(e.target.value);
    props.onRefreshCalculate({ [e.target.name]: value });
  };

  React.useEffect(() => {
    props.onRefreshCalculate({});
  }, []);

  let months = [
    { value: 12, name: "1 nm (12 thng)" },
    { value: 24, name: "2 nm (24 thng)" },
    { value: 36, name: "3 nm (36 thng)" },
    { value: 48, name: "4 nm (48 thng)" },
    { value: 60, name: "5 nm (60 thng)" },
    { value: 72, name: "6 nm (72 thng)" },
    { value: 84, name: "7 nm (84 thng)" },
  ];

  const classes = useStyles();

  const isMobile = () => {
    const userAgent =
      global["navigator"]?.userAgent || global["navigator"]?.vendor;

    return (
      /windows phone/i.test(userAgent) ||
      /android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
        userAgent
      )
    );
  };

  const renderDescription = () => {
    return (
      <div>
        <h2>Th tc mua xe tr gp cng Hino Vlog:</h2>
        <p>
          1. Mua xe ti v k hp ng cng Hino Vlog ti i l Hino chnh
          hng.
        </p>
        <p>2. Hino Vlog chun b h s cho khch hng.</p>
        <p>
          3. Ngn hng thm nh (gi in thoi hi thm sau  s trc tip
          xung nh).
        </p>
        <p>4. Ngn hng gi thng bo cho vay n i l Hino chnh hng.</p>
        <p>
          5. i l Hino s gi cho khch hng v khch hng thanh ton t 2 
          tin hnh ng thng theo yu cu khch hng v i ng k xe.
        </p>
        <p>6. Khch hng kim tra xe trc khi gii ngn.</p>
        <p>7. Ngn hng gii ngn.</p>
        <p>8. Khch hng nhn ton b h s xe v nhn xe.</p>
        <p>9. Hon thnh.</p>
        <p>
          <i>
            Quy trnh mua xe tr gp cng Hino Vlog s hon thnh t 20-25 ngy
            ty vo dng xe cng nh tha thun gia Khch hng v i l Hino
            chnh hng.
          </i>
        </p>
        <h2>H s i vi khch hng c nhn:</h2>
        <p>1. Chng minh nhn dn hoc Th cn cc cng dn.</p>
        <p>2. S h khu.</p>
        <p>3. Giy ng k ht hn hoc giy chng nhn c thn.</p>
        <p>4. Hp ng mua xe.</p>
        <p>5. Chng minh thu nhp (c th c hoc khng).</p>
        <h2>H s i vi khch hng doanh nghip:</h2>
        <p>1. Gip php ng k kinh doanh (Hot ng trn 6 thng).</p>
        <p>2. Thng tin ca ch doanh nghip.</p>
        <p>3. Bo co ti chnh gn y nht.</p>
        <p>4. T khai quyt ton doanh nghip gn y nht.</p>
        <p>5. iu l cng ty.</p>
        <p>6. Hp ng mua xe.</p>
      </div>
    );
  };

  let data = props.tableCalculate;

  return (
    <div>
      <Grid container={!isMobile()}>
        <Grid item sm={5}>
          <FormControl className={classes.formControl}>
            <label htmlFor="carPrice" style={{ fontWeight: "bold" }}>
              Gi xe
            </label>
            <NumberFormat
              key={"carPrice"}
              id="carPrice"
              customInput={TextField}
              onValueChange={(e) => {
                onChangeField("carPrice", e.floatValue);
              }}
              value={data.carPrice}
              thousandSeparator={true}
              suffix={" "}
            />
          </FormControl>
          <FormControl className={classes.formControl}>
            <label htmlFor="borrowingPrice" style={{ fontWeight: "bold" }}>
              S tin cn vay (VN)
            </label>
            <NumberFormat
              id="borrowingPrice"
              customInput={TextField}
              onValueChange={(e) => {
                onChangeField("borrowingPrice", e.floatValue);
              }}
              value={data.borrowingPrice}
              thousandSeparator={true}
              suffix={" "}
            />
          </FormControl>
          <FormControl className={classes.formControl}>
            <label htmlFor="month" style={{ fontWeight: "bold" }}>
              Thi gian vay (Thng)
            </label>
            <NativeSelect
              className={classes.selectBox}
              value={data.month}
              onChange={(e) => {
                onChangeSelect(e);
              }}
              inputProps={{ name: "month", id: "month" }}
            >
              {/* <option key={0} value={0}>-- Vui lng chn --</option> */}
              {months.map((e) => (
                <option key={e.value} value={e.value}>
                  {e.name}
                </option>
              ))}
            </NativeSelect>
          </FormControl>
          <FormControl className={classes.formControl}>
            <label htmlFor="prepayPercent" style={{ fontWeight: "bold" }}>
              Ngn hng h tr (%)
            </label>
            <NumberFormat
              id="prepayPercent"
              customInput={TextField}
              onValueChange={(e) => {
                onChangeField("prepayPercent", e.floatValue);
              }}
              value={data.prepayPercent}
              thousandSeparator={true}
              suffix={" %"}
              disabled={true}
            />
          </FormControl>
          <FormControl className={classes.formControl}>
            <label htmlFor="precentBankBefore" style={{ fontWeight: "bold" }}>
              Li sut vay nm (c nh 6 thng u)
            </label>
            <NumberFormat
              id="precentBankBefore"
              customInput={TextField}
              value={data.precentBankBefore * 100}
              thousandSeparator={true}
              suffix={" %"}
              disabled={true}
            />
          </FormControl>
          <FormControl className={classes.formControl}>
            <label htmlFor="precentBankAfter" style={{ fontWeight: "bold" }}>
              Li sut vay nm (sau iu chnh)
            </label>
            <NumberFormat
              id="precentBankAfter"
              customInput={TextField}
              value={data.precentBankAfter * 100}
              thousandSeparator={true}
              suffix={" %"}
              disabled={true}
            />
          </FormControl>

          {!isMobile() && renderDescription()}
        </Grid>
        <Grid item sm={7}>
          <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell align="right" style={{ fontWeight: "bold" }}>
                    K
                  </TableCell>
                  <TableCell align="right" style={{ fontWeight: "bold" }}>
                    S tr nh k
                  </TableCell>
                  <TableCell align="right" style={{ fontWeight: "bold" }}>
                    Li
                  </TableCell>
                  <TableCell align="right" style={{ fontWeight: "bold" }}>
                    Gc
                  </TableCell>
                  <TableCell align="right" style={{ fontWeight: "bold" }}>
                    D n
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {data.rows.map((row, i) => (
                  <TableRow
                    className={
                      classes[
                        i > 0 && i <= 6
                          ? "sixPercent"
                          : i + 1 == data.rows.length
                          ? "rowTotal"
                          : "tenPercent"
                      ]
                    }
                    key={row.period}
                  >
                    <TableCell align="right">{row.period}</TableCell>
                    <TableCell align="right">
                      <NumberFormat
                        value={row.recurring_number}
                        displayType={"text"}
                        thousandSeparator={true}
                        suffix={" "}
                      />
                    </TableCell>
                    <TableCell align="right">
                      <NumberFormat
                        value={row.interest}
                        displayType={"text"}
                        thousandSeparator={true}
                        suffix={" "}
                      />
                    </TableCell>
                    <TableCell align="right">
                      <NumberFormat
                        value={row.origin}
                        displayType={"text"}
                        thousandSeparator={true}
                        suffix={" "}
                      />
                    </TableCell>
                    <TableCell align="right">
                      <NumberFormat
                        value={row.debt}
                        displayType={"text"}
                        thousandSeparator={true}
                        suffix={" "}
                      />
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
      </Grid>

      <Grid>{isMobile() && renderDescription()}</Grid>
    </div>
  );
};
